@import url(https://fonts.googleapis.com/css2?family=Playball&display=swap);
* {
  margin: 0;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header {
  display: flex;
  align-items: baseline;
  justify-content: space-evenly;
  padding: 10px;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 100;
  background-color: white;
  color: rgb(35, 82, 35);

}

.header__logo {
  /* height: 25px;   
  object-fit: contain; */
  font-size: 40px;
  font-family: 'Playball', cursive;
} 

.header__center {
  font-weight: bold;
  display: flex;
  align-items: center;
}

.header__center > h3 {
  display: flex;
  align-items: center;
  margin-right: 20px;
}

.link {
  color: rgb(35, 82, 35);
  text-decoration: none;
}

.link:hover {
  color: rgb(54, 126, 72);
}

.header__right {
  display: flex;
  align-items: center;
}

.App {
    font-family: Arial, Helvetica, sans-serif;
    margin: auto;
    text-align: center;
    vertical-align: baseline;
}
.productCard {
    margin-bottom: 40px;
    width: 270px;
    margin: auto;

}

.productCard > img {
    height: 300px;
    width: 300px;
    object-fit: cover;
    border-color: rgba(255, 255, 255, 0.911);
    border-radius: 10px;
    border-width: 50px;
}
.products {
    display: flex;
    flex-wrap: wrap;
    padding-inline: 200px;
    padding-block: 50px;
    margin: auto;
}


.cover > img {
    height: auto;
    width: 100%;
    object-fit: cover;
    display: flex;
    align-items: center;
}

@media screen and (max-width: 800px) {
    .cover {
    height: 200px;
    }
}

