.order {
    display: inline-flexbox;
    align-items: center;
    padding-inline: 200px;
    padding-bottom: 50px;
}
.about {
    display: inline-flexbox;
    align-items: center;
    padding-inline: 200px;
    padding-bottom: 50px;
}
.footer {
    padding-block: 10px;
    display: flex;
    justify-content: center;
    width: 100%;
    background-color: rgba(35, 82, 35, 0.219);
}
